	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Sunset Clause","date":"07 May 2001","slug":"/2001-05-07-sunset-clause/","tags":"Y2001"},"html":"<p>As you might have guessed, the idyll on the patio last evening didn't last very long. </p>\n<p>Today, we sent Grandma and Grandpa on their merry way back to Minnesota. Tomorrow, Mama returns (for a couple of days), Christopher goes back to day care, and I go back after two weeks to a refreshing day at the office.</p>\n<p>Having left my parents early and moved far away, our infrequent but long visits tend to leave me with mixed feelings (there is nothing like your parents to make you feel childish). But Christopher is now something additional that we have in common.</p>\n<p>Well, what you know, my employer made a profit last quarter. A whole Euro 15000. I guess that's what our CEO meant when she was hoping for a 'black zero' for the quarter.</p>"},"next":{"fields":{"title":"Patio Life","date":"06 May 2001","slug":"/2001-05-06-patio-life/"}},"previous":{"fields":{"title":"Daredevil","date":"08 May 2001","slug":"/2001-05-08-daredevil/"}}},{"node":{"fields":{"title":"Patio Life","date":"06 May 2001","slug":"/2001-05-06-patio-life/","tags":"Y2001"},"html":"<p>We're posting this from the patio. It's sunny, not all that warm, and a bit windy at 6 pm.</p>\n<p>It's my folks' last day here. Mama's away at a convention for work.</p>"},"next":{"fields":{"title":"Pop Culture Spreads Thin","date":"05 May 2001","slug":"/2001-05-05-pop-culture-spreads-thin/"}},"previous":{"fields":{"title":"Sunset Clause","date":"07 May 2001","slug":"/2001-05-07-sunset-clause/"}}},{"node":{"fields":{"title":"Pop Culture Spreads Thin","date":"05 May 2001","slug":"/2001-05-05-pop-culture-spreads-thin/","tags":"Y2001"},"html":"<p>An awful lot of webloggers have admitted to watching 'Survivor'. Could one of you please explain to <a href=\"http://andrea.editthispage.com/2001/05/03\">Andrea</a> what it is? She says that without a TV of her own, she hasn't been able to keep up with how low the niveau of modern television has fallen. (Then again, maybe she doesn't really want to know.)</p>"},"next":{"fields":{"title":"DV Hello World","date":"04 May 2001","slug":"/2001-05-04-dv-hello-world/"}},"previous":{"fields":{"title":"Patio Life","date":"06 May 2001","slug":"/2001-05-06-patio-life/"}}}],"pathPrefix":"","first":false,"last":false,"index":1006,"pageCount":1101}}

